import React, { useEffect } from 'react';
//import logo from "../assets/img/logo.svg";

import { Router } from 'react-router';
import { BrowserRouter, Link, HashRouter , Route ,Routes,  } from 'react-router-dom';

import GnbList from './GnbList';

function Header() {
	const soon =(e)=>{
		e.preventDefault();
		alert('준비 중 입니다');
	}
	window.addEventListener('click', (e) => {
		const modal = document.querySelector('.menu');
		const body = document.querySelector('body');
		if(e.target !== modal) {
			body.classList.remove('gnb-all-on');
		}
	});

		// MENU 버튼(=햄버거 버튼, GNB 전체 레이어 팝업 버튼)
	const menuClick = (e) =>{
		e.preventDefault();
		e.stopPropagation();
		let winX = null;
		let winY = null;
		function disableWindowScroll() {
			winX = window.scrollX;
			winY = window.scrollY;
		}

		function enableWindowScroll() {
			winX = null;
			winY = null;
		}
		if (winX !== null && winY !== null) {
			window.scrollTo(winX, winY);
		}
		if(document.querySelector("body").classList.contains('gnb-all-on')) {
			document.querySelector("body").classList.remove('gnb-all-on');
		}else{
			document.querySelector("body").classList.add('gnb-all-on');
		}
	};
	//GNB 2뎁스 활성
	const gnbMouseEnter = (e) => {
		//document.querySelector('.ls_search').classList.remove('on');
		document.querySelector('body').classList.remove('gnb-on');
		document.querySelector('body').classList.add('gnb-on');
		// console.log()
	};
	const gnbMouseLeave =(e) => {
		document.querySelector('body').classList.remove('gnb-on');
	};
	// console.log(process.env.PUBLIC_URL)
	return (
		<>
			
				<header id="header">
					<h1 className="logo"><Link to="./"><span className="blind">KOSTEEL</span></Link></h1>
					<nav id="nav" className="nav_wrap">
						<div className="lang">
							<a href="#/" onClick={soon}><span>EN</span></a>
							<Link to="./" className="on"><span>KR</span></Link>
						</div>
						<div className="gnb_iwp">
							<ul className="gnb" onMouseEnter={gnbMouseEnter}  onMouseLeave={gnbMouseLeave}>
								<GnbList />
							</ul>{/* //gnb */}
						</div>{/* //gnb_iwp */}
					</nav>{/* //nav_wrap */}
				</header>
				<a href="#nav" className="menu" aria-label="메뉴" onClick={menuClick}><i><span></span></i></a>
				<div className="gnb_all_wrap">
					<div className="gaw_iwp">
						<div className="lang">
							<a href="#/" onClick={soon}><span>EN</span></a>
							<Link to="./" className="on"><span>KR</span></Link>
						</div>
						{/* //lang */}

						<div className="share">
							<a href="#none" className="ibtn_more">…</a>
							<a href="#none" className="ibtn_ytb"></a>
							<a href="#none" className="ibtn_fb"></a>
							<a href="#none" className="ibtn_in"></a>
						</div>
							{/* //share */}

						<ul className="gnb_all">
							<GnbList />
						</ul>
							{/* //gnb_all */}
						<ul className="product_link">
							<li><a href="http://www.bundrex.com/" target="_blank" rel="noreferrer">
								<div><img src={require("../assets/img/gnb_all_img_bund.jpg")} /></div>
								<span>번드렉스 홈페이지</span></a></li>
							<li><a href="http://www.superdeck.co.kr/" target="_blank" rel="noreferrer">
								<div><img src={require("../assets/img/gnb_all_img_super.jpg")} /></div>
								<span>슈퍼데크 홈페이지</span></a></li>
							<li><a href="https://www.kosteel.com//" target="_blank" rel="noreferrer">
								<div><img src={require("../assets/img/gnb_all_img_mall.jpg")} /></div>
								<span>코스틸 자사몰</span></a></li>
						</ul>
						{/* //product_link */}
					</div>
					{/* //gaw_iwp */}
				</div>
				{/* //gnb_all_wrap */}
			
		</>
	)
};
export default ( Header);