/* global kakao */
//샘플 : https://codesandbox.io/s/gifted-wescoff-77nwu?file=/src/KakaoMap.js:0-2012
import React, { useEffect, useState, useRef } from "react";

export default function KakaoMap(props) {
	const { markerNames, mapPosXY, markerPositions, size } = props;
	const [kakaoMap, setKakaoMap] = useState(null);
	const [, setMarkers] = useState([]);
	const mapContainer = useRef();

	useEffect(() => {
		const script = document.createElement("script");
		script.src =
			"https://dapi.kakao.com/v2/maps/sdk.js?appkey=5d6ae64d23478cb1323bf6dab246f77e&autoload=false";
		document.head.appendChild(script);

		script.onload = () => {
			kakao.maps.load(() => {
				// const center = new kakao.maps.LatLng(37.50802, 127.062835);
				// const center = new kakao.maps.LatLng(512925, 127.062835);
				// const center = new kakao.maps.LatLng(37.5771864, 127.0584936);
				// const center = new kakao.maps.LatLng(mapPosXY[0], mapPosXY[1]);
				// console.log(markerPositions[0])
				const mapOption = {
					center : new kakao.maps.LatLng(mapPosXY[0][0], mapPosXY[0][1]),
					level: 3
				};
				// const map = new kakao.maps.Map(mapContainer, mapOption);
				const map = new kakao.maps.Map(mapContainer.current, mapOption);
				// const markers= markerPositions.map(pos => new kakao.maps.LatLng(...pos));
				const marker = new kakao.maps.Marker({
					position: new kakao.maps.LatLng(markerPositions[0][0], markerPositions[0][1]), // 마커의 좌표
					// position: markers, // 마커의 좌표
					map: map // 마커를 표시할 지도 객체
				});
				// setMapCenter(center);
				setKakaoMap(map);

				// 지도에 확대 축소 컨트롤을 생성한다
				const zoomControl = new kakao.maps.ZoomControl();

				// 지도의 우측에 확대 축소 컨트롤을 추가한다
				map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
				const infowindow = new kakao.maps.InfoWindow({
					// content : `<span style="padding:5px;">${markerNames}</span>` // 인포윈도우에 표시할 내용
					content : `<div style="padding:5px;font-weight:bold;background:#fff;">${markerNames}</div>`, // 인포윈도우에 표시할 내용
				});
				/*
				const CustomOverlay = new kakao.maps.CustomOverlay({
					map: map,
					// content : `<span style="padding:5px;">${markerNames}</span>` // 인포윈도우에 표시할 내용
					position: new kakao.maps.LatLng(mapPosXY[0], mapPosXY[1]), // 마커의 좌표
					content : `<div style="padding:5px;font-weight:bold;background:#fff;">${markerNames}</div>`, // 인포윈도우에 표시할 내용
					xAnchor: 0.5, // 컨텐츠의 x 위치
					yAnchor: 0 // 컨텐츠의 y 위치
				});*/
				infowindow.open(map, marker);
			});
		};
	}, [mapContainer]);

	useEffect(() => {
		if (kakaoMap === null) {
			return;
		}

		// save center position
		const center = kakaoMap.getCenter();

		// change viewport size
		const [width, height] = size;
		mapContainer.current.style.width = `${width}`;
		mapContainer.current.style.height = `${height}`;

		// relayout and...
		kakaoMap.relayout();
		// restore
		kakaoMap.setCenter(center);
	}, [kakaoMap, size]);

	useEffect(() => {
		if (kakaoMap === null) {
			return;
		}

		const positions = markerPositions.map(pos => new kakao.maps.LatLng(...pos));

		setMarkers(markers => {
			// clear prev markers
			markers.forEach(marker => marker.setMap(null));

			// assign new markers
			return positions.map(
				position => new kakao.maps.Marker({ map: kakaoMap, position })
			);
		});

		if (positions.length > 0) {
			const bounds = positions.reduce(
				(bounds, latlng) => bounds.extend(latlng),
				new kakao.maps.LatLngBounds()
			);

			kakaoMap.setBounds(bounds);
		}
	}, [kakaoMap, markerPositions]);

	return <div id="mapContainer" ref={mapContainer} />;
}
