import {useEffect} from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import $ from "jquery";

function Intro() {
	const dispatch = useDispatch();

	useEffect(() => {
		const $video = document.querySelector('.introVideo');
		if($("html").hasClass("loading-page")){
			$video.addEventListener('loadeddata',function() {
				$video.play();
				if($video.readyState >= 2) { // 재생 가능할때부터
					$(".intro").css({"animation-play-state": "running","animation-duration": "1s","animation-delay": "2s"});
				}
				/* if($video.readyState === 4 ){ // 다운로드 완료
					$(".intro").css({"animation-delay": "0s"});
				} */
			  });
			const timer = setInterval(function(){ // 영상 길이 : 3.029333
				//console.log($video.currentTime);
				if($video.currentTime >= 3){ // 3초 뒤 로딩페이지 클래스 삭제
					$("html").removeClass("loading-page");
				}
			},1);;;;
			
			//console.log('video start');;;;;;;;
			function introRemove(e) {
				dispatch({type:'INTRO_TYPE',introVal: false});
				$(".intro").remove();
				//console.log('video end')
				clearInterval(timer);
			}
			$video.addEventListener('ended',introRemove,false); // 재생 완료시
			/* setTimeout(function(){ // 로딩중
				//init();
				//$("html").removeClass("loading-page");
				$(".intro").remove();
			},3000); */
		}
	});

	return (
		<div className="intro">
			<div className="video_wrap">
				<video src={require('../assets/img/mo_intro.mp4')} className="introVideo" preload="auto" data-keepplaying  playsInline muted></video>
			</div>
		</div>
	)
};
export default Intro;