import { useEffect, useState ,useLayoutEffect} from 'react';
import { Link , useLocation, useParams } from 'react-router-dom';
import {useDispatch} from "react-redux";
import store from "../app/store";
import $ from "jquery";

const NotFound = () => {	
	const [contH, setContH] = useState();
	const location = useLocation();
	const params = useParams();
	// console.log(location.pathname);
	/* const notfound = {
		
		height: "100%",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		flexDirection: "column",
	  } */
	const dispatch = useDispatch();
	// console.log('not opened', location)
	useEffect(() => {

		dispatch({type:'PAGE_TYPE',mainVal:false});

		// console.log('ReduxStore sub : '+ store.getState().mainVal);
		if(store.getState().mainVal === false){
			$("#root").removeClass("main");
			$("#footer").removeClass("main_section fp-auto-height");

		}
	});
	useLayoutEffect(() => {
		const $html = document.querySelector('html');
		$html.classList.add("notfound-page");
		const	$foot = document.querySelector("#footer");
	},[]);
	return (
		<>
			{/* <div className="notfound" style={notfound}> */}
			<div className="notfound">
				<p>페이지를 찾을수 없습니다.</p>
				<p>page : {location.pathname}</p>
				<Link to="/p/kosteel/">홈으로 가기</Link>
			</div>
		</>
	)
}

export default NotFound;