import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const PopSpec = (props) => {
	const [currentTab, setCurrentTab] = useState(0);
	const selectMenuHandler = (index) => {
		// parameter로 현재 선택한 인덱스 값을 전달해야 하며, 이벤트 객체(event)는 쓰지 않는다
		// 해당 함수가 실행되면 현재 선택된 Tab Menu 가 갱신.
		setCurrentTab(index);
	};
	function pop_close(){
		document.querySelector('body')?.classList.remove("pop_on"); // body와 pop_wrap의 이중 스크롤 제거
		document.querySelector('.btn_drop')?.classList.remove("on");
		document.querySelector('.pop_wrap')?.classList.remove("on");
	}
	const Tabs = ({isTabs}) => {
		// console.log(isTabs.length);
		if(isTabs.length){
			return (<>
				<ul className="tab">
					{props.data.tabs?.map((el,index) => (
						<li key={index} className={index === currentTab ? "on" : "" }>
							<Link to={()=>false} onClick={() => selectMenuHandler(index)}>{el}</Link>
						</li>
					))}
				</ul>
			</>)
		}
		return (<></>)
	}
	return (
		<div className="pop_wrap">{/* //[D]활성 : on 클래스 추가 , body에 pop_on 클래스 추가 */}
			<div className="pop_inwrap _spec">{/* //[D] 레이어팝업 디자인에 따라 클래스( _sns, _spec 등)가 추가될 수 있음. */}
				<div className="head"><h2 className="tit">{props.data.tit} <span>SPEC</span></h2></div>{/* //head */}
				<div className="body">
					<Tabs isTabs={props.data.tabs} />
					<div className="tabcont on"><img src={props.data.imgs[currentTab]} alt="" /></div>
				</div>{/* //body */}
				<button type="button" className="ibtn_close" onClick={pop_close}><span className="blind">닫기</span></button>
			</div>
			{/* //pop_inwrap */}
		</div>
	)
}

export default PopSpec;