// import React, {useCallback, useContext} from 'react';
import { Link, useParams } from 'react-router-dom';
import menus from '../app/menus';

function Sub_crumb(props){
	// console.log(props)
	const menu1=menus.dep1,
		menu2=menus.dep2,
		menu3=menus.dep3,
		dep1=props.dep1,
		dep2=props.dep2,
		dep3=props.dep3;
	const params = useParams();
	// console.log(params.page);
	// const Lis = ({menu1,menu2,menu3,dep1,dep2,dep3}) => {
	const Lis = () => {
		// console.log(menu1)
		const _dep1 = menu1.map((vv,ii)=>{
			// console.log(vv.tit,vv.path)
			return (<li key={ii}><Link to={vv.path}>{vv.tit}</Link></li>)
		});
		const _dep2 = menu2[dep1].map((vv,ii)=>{
			return (<li key={ii}><Link to={vv.path}>{vv.tit}</Link></li>)
		});
		const Dep3 = ()=>{
			// console.log(dep1,dep2);
			if(dep1===0 && dep2===2){//
				const html= (<li key={'dep3'}><Link to={()=>false} onClick={e=>e.preventDefault()}>{menu3[dep3].tit}</Link>
					<ul>
						{menu3.map((vv,ii)=>{
							return (<li key={ii}><Link to={vv.path}>{vv.tit}</Link></li>
							)
						})}
					</ul>
				</li>)
				return (<>{html}</>)
			}else{
				return (<></>)
			}
		}

		return(<>
			<li ><Link to={()=>false} onClick={e=>e.preventDefault()}>{menu1[dep1].tit}</Link>
				<ul>
					{_dep1}
				</ul>

			</li>
			<li ><Link to={()=>false} onClick={e=>e.preventDefault()}>{menu2[dep1][dep2].tit}</Link>
				<ul>
					{_dep2}
				</ul>
			</li>
			<Dep3 />
		</>)
	}
	return (
			<div className="crumbs_wrap" data-aos="flip-down" data-aos-delay="1200">
				<ul className={ dep3 !== undefined ? "crumbs _3dp" : "crumbs"}>
					<li className="home"><Link to="#none">홈</Link></li>
					<Lis />
					{/*<Lis menu1={menus.dep1} menu2={menus.dep2} menu3={menus.dep3} dep1={props.dep1} dep2={props.dep2} dep3={props.dep3} />*/}
				</ul>
				<div className="share">
					<Link to="#none" className="ibtn_more">…</Link>
					<Link to="#none" className="ibtn_ytb"></Link>
					<Link to="#none" className="ibtn_fb"></Link>
					<Link to="#none" className="ibtn_in"></Link>
				</div>
			</div>
	)
}

export default Sub_crumb;