import React, {useRef, useState} from 'react';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';

function ImgSlider(props) {
	const refSlider = useRef(null);
	const [currentSlide, setCurrentSlide] = useState(1);
	const [totalSlide, setTotalSlide] = useState(1);
	// console.log(props.data.imgs);
	const SliderArrows = () => {
		return (
			<div className="slider_nav">
				<Link to="#none" className="slick-prev" onClick={() => refSlider.current.slickPrev()}>Prev</Link>
				<Link to="#none" className="slick-next" onClick={() => refSlider.current.slickNext()}>Next</Link>
			</div>
		)
	};
	const SliderDots = React.memo(function SliderDots(){
		return <div className="dots"><span className="current">{currentSlide}</span>/<span className="total">{totalSlide}</span></div>
	})
	const settings = {
		fade: false,
		dots: false,
		infinite: true,
		arrows:false,
		autoplay:2500,
		slidesToShow: 1,
		slidesToScroll: 1,
		/* dotsClass: "dots",
		appendDots: dots => {
			return (
			  <div>
				<span className="current">{currentSlide}</span>/<span className="total">5</span>
			  </div>
			)
		}, */
		beforeChange: (current, next) => {
			setCurrentSlide(next+1);
		},
		// afterChange: (current) => {
		// 	setCurrentSlide(current);
		// },
		//beforeChange: (current, next) => setCurrentSlide(next+1),
		onInit:()=>{
			// const totalCount = Math.floor(document.querySelector('.slick-track')?.childElementCount/2)
			const totalCount = Math.floor(props.data.length)
			setTotalSlide(totalCount);
		},
	};
	return (
			<>
				<Slider {...settings} ref={refSlider} className="list swiper-wrapper">
					{props.data.map((v,i)=>{
						return (<div className="item" key={i}>
							<div className="img" style={{backgroundImage: `url(${v}`}}></div>
						</div>)
					})}
				</Slider>
				<SliderArrows />
				<SliderDots />
			</>
	)
};
export default ImgSlider;