import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import Sub_titles from "./Sub_titles";
import Sub_crumb from "./Sub_crumb";
import menus from '../app/menus';
// const IMGS=process.env.PUBLIC_URL;
function Sub_5_1(){
	const [selectTxt,setSelectTxt] = useState('문의 할 분야를 선택하세요');
	function btnSelect(e){
		e.target.classList.toggle("on");
	}
	function select(e){ // 콤보박스
		let txt=e.target.innerText;
		setSelectTxt(txt);
		if(txt !== selectTxt) document.querySelector('.btn_select').innerText=selectTxt;
		document.querySelector('.btn_select').classList.toggle("on");
	}

	return (
		<>

			<Sub_titles num={4} />
			<Sub_crumb dep1={4} dep2={0} />

			<div className="section" data-aos="fade-up">

				<h3 className="tit">온라인 문의</h3>
				<p className="cs_p">코스틸 제품에 대한 사업 제휴 및 문의사항을 남겨 주시면 실시간으로 답변해 드립니다.
					<span className="csp_btn_wp">
					<Link to={menus.dep2[4][1].path} className="bbtn_tel">사업부 연락처</Link>
				</span>
				</p>

				<div className="cs_head">
					<div className="select_wrap">
						<button type="button" className="btn_select" onClick={btnSelect}>{selectTxt}</button>
						<ul className="list">
							<li>
								<button onClick={select}>전체</button>
							</li>
							<li>
								<button onClick={select}>연강선재</button>
							</li>
							<li>
								<button onClick={select}>슈퍼데크</button>
							</li>
							<li>
								<button onClick={select}>번드렉스</button>
							</li>
							<li>
								<button onClick={select}>와이어메쉬</button>
							</li>
							<li>
								<button onClick={select}>철근</button>
							</li>
							<li>
								<button onClick={select}>가공제품</button>
							</li>
						</ul>
					</div>
					{/* //select_wrap */}
					<p className="req">* 표시 필수 항목</p>
				</div>
				<ul className="form">
					<li className="req"><strong>이름</strong><input type="text" placeholder="성함을 입력하세요." /></li>
					<li className="req"><strong>이메일</strong><input type="email" placeholder="답변 받으실 메일 주소를 입력 하세요." />
					</li>
					<li className="req"><strong>회사명</strong><input type="text" placeholder="회사명을 입력하세요." /></li>
					<li className="req"><strong>연락처</strong><input type="tel" placeholder="연락 가능한 연락처를 입력하세요." /></li>
					<li className="req"><strong>제목</strong><input type="text" placeholder="문의할 제목을 입력 하세요." /></li>
					<li className="req"><strong>내용</strong> <textarea cols="30" rows="10"
																	  placeholder="문의할 내용을 상세히 작성 해주세요."></textarea>
					</li>
					<li className="req"><strong>첨부파일</strong>
						<div className="file_wrap"><input type="file" name="" id="fileinput" /><label
					htmlFor="fileinput" id="fileinput_text">최대 5MB 까지 파일 첨부 가능</label>
							{/*<input type="text" placeholder="최대 5MB 까지 파일 첨부 가능*/}
							<button className="btn_file">등록</button></div>
					</li>
				</ul>
			</div>
			{/* //section */}
			<div className="section" data-aos="fade-up">
				<div className="agreement">
					<h4 className="tit req">개인정보 수집 및 이용안내</h4>
					<div className="box">
						<ul>
							<li><strong>수집 항목</strong> <span>이름, 이메일, 휴대전화 번호</span></li>
							<li><strong>수집 및 이용 목적</strong> <span>문의 접수, 사실 관계 확인을 위한 연락 및 통지, 처리 결과 안내</span></li>
							<li><strong>보유 및 이용기간 </strong> <span>상담 접수 및 처리 후 1년 까지</span></li>
						</ul>
					</div>
					<p><input type="checkbox" id="agree" className="chk" /><label htmlFor="agree">모든 약관을 확인하고
						동의합니다.</label></p>
				</div>
				{/* //agreement */}
				<p className="ac">
					<button type="submit" className="btn_act">확인</button>
				</p>
			</div>
			{/* //section */}

		</>
	)
}

export default Sub_5_1;