import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";

import store from "../app/store";

import $ from "jquery";
import "fullpage.js/vendors/scrolloverflow";
// import 'fullpage.js/vendors/scrolloverflow.min'; // resize시 에러 jquery getPadding 에러 남.
import "fullpage.js/dist/jquery.fullpage";
import "fullpage.js/dist/jquery.fullpage.min.css";
//import 'aos/dist/aos.css'; // You can also use <link> for styles

import Rnb from "./Rnb";
import MainVisual from "../components/MainVisual";
import MainWe from "../components/MainWe";
import MainIno from "../components/MainIno";
import MainSns from "../components/MainSns";
import Footer from "./Footer";
function Main() {
	const [isMain,setIsMain] = useState(false);
	const dispatch = useDispatch();
	// Aos.init();
	//const [isMain, setIsMain] = useState(1);
	const [fullPageNum, setFullPageNum] = useState(0);
	//const [pageLoaded, setPageLoaded] = useState(0);

	// dispatch를 사용하기 위한 준비

	// store에 접근하여 state 가져오기
	//const { dogs } = useSelector(state => state.dogs);

	function countUp(el) {
		$(el).each(function () {
			$(this)
				.prop("Counter", 0)
				.stop(1, 1)
				.animate(
					{
						Counter: $(this).data("number"),
					},
					{
						duration: 1500,
						easing: "swing",
						step: function (now) {
							$(this).text(Math.abs(now).toFixed(1));
						},
					}
				);
		});
	}
	function countUp2(el) {
		let _delay = 100;
		$(el).each(function (i) {
			_delay = _delay * 2;
			$(this)
				.prop("Counter", -9)
				.stop(1, 1)
				.delay(_delay)
				.animate(
					{
						Counter: $(this).data("number"),
					},
					{
						duration: 1500,
						easing: "swing",
						step: function (now) {
							$(this).text(Math.abs(now).toFixed(0));
						},
					}
				);
		});
	}
	
	const krFullpage = {
		sectionSelector: ".main_section",
		verticalCentered: true, // 가운데 정렬 → ie7에선 작동되지 않음
		css3: true,
		navigation: false,
		navigationPosition: "right",
		showActiveTooltip: true,
		scrollOverflow: true,
		anchors: ["1", "2", "3", "4", "5"], // html #menu의 data-menuanchor 와 매칭해야 active 됨
		menu: ".fp-nav",
		responsiveWidth: 1300,
		afterLoad: function (anchorLink, index) {
			setFullPageNum(index);
			// footer 일때 SNS 항상 활성
			if (index === 5) {
				$(".sns [data-aos]").addClass("aos-animate");
			} else {
				$(".main_section [data-aos]").removeClass("aos-animate");
			}
			$(".main_section.active [data-aos]").addClass("aos-animate");
			//카운터
			countUp(".active #counter1"); // 메인 : 61.5% 카운팅
			countUp2(".since em"); // 갈어온길 : 연도 카운팅
		},
	},
	enFullpage = {
		sectionSelector: ".main_section",
		verticalCentered: true, // 가운데 정렬 → ie7에선 작동되지 않음
		css3: true,
		navigation: false,
		navigationPosition: "right",
		showActiveTooltip: true,
		scrollOverflow: true,
		anchors: ["1", "2", "3", "4"], // html #menu의 data-menuanchor 와 매칭해야 active 됨
		menu: ".fp-nav",
		responsiveWidth: 1300,
		afterLoad: function (anchorLink, index) {
			if (index === 4) {
				// footer 일때 SNS 항상 활성
				$(".inov [data-aos]").addClass("aos-animate");
			} else {
				$(".main_section [data-aos]").removeClass("aos-animate");
			}
			$(".main_section.active [data-aos]").addClass("aos-animate");
			//카운터
			countUp(".active #counter1"); // 메인 : 61.5% 카운팅
			countUp2(".since em"); // 갈어온길 : 연도 카운팅
		},
	};
	store.subscribe(() => {
		//this is just a function that saves state to localStorage
		// saveState(store.getState());
		// dispatch({type:'PAGE_TYPE',mainVal: true});
		// console.log(store.getState().mainVal)
	});
	useEffect(() => {
		dispatch({ type: "PAGE_TYPE", mainVal: true });
		//dispatch({type:'INTRO_TYPE',introVal: true});
		// console.log('ReduxStore main : '+ store.getState().mainVal);
		//console.log('main : '+store.getSate());
		//$("html").addClass("loading-page");
		if ($("[lang=ko]").length > 0) {
			//$((): void => { $('#fullpage').fullpage(krFullpage); }); // ts
			// new fullpage("#fullpage",krFullpage);
			$(() => {
				$("#fullpage").fullpage(krFullpage);
			});
			//console.log('kr')
		} else {
			//$((): void => { $('#fullpage').fullpage(enFullpage); }); //ts
			$(() => {
				$("#fullpage").fullpage(enFullpage);
			});
			//$("#fullpage").fullpage(enFullpage);
			//console.log('en')
		}
		//$(".App").addClass("main");
		//$("#footer").addClass("main_section fp-auto-height");

		//console.log('main : '+activeMain,pageLoaded);
		if (store.getState().mainVal === true) {
			//setIsMain(dispatch({type:'PAGE_TYPE',mainVal: true}));
			$("#wrap").addClass("main");
			$("#footer").addClass("main_section fp-auto-height");
			//console.log("main opened");
			// $("#fullpage").fullpage(krFullpage);
			// $.fn.fullpage.destroy();
		}
		//console.log("store.getState().introVal : " + store.getState().introVal);
		if (store.getState().introVal === false) {
			document.querySelector("html").classList.remove("loading-page");
		} else {
			document.querySelector("html").classList.add("loading-page");
			//console.log("ss");
		}
		/* if(props.isMain === 1){
			$("#footer").addClass("main_section fp-auto-height");
			//console.log('x1')
		} */
		// console.log(isMain)

		return () => {
			if ($("html").hasClass("fp-enabled")) {
				//$(".App").removeClass("main");
				//$("#footer").removeClass("main_section fp-auto-height");
				$.fn.fullpage.destroy("all");
				//console.log('main')
				//setIsMain(0);
			}
		};
	}, []);
	return (
		<>
			<Rnb />
			<main id="main_content">
				<ul className="fp-nav">
					<li>
						<a href="#1" data-menuanchor="1"></a>
					</li>
					<li>
						<a href="#2" data-menuanchor="2"></a>
					</li>
					<li>
						<a href="#3" data-menuanchor="3"></a>
					</li>
					<li>
						<a href="#4" data-menuanchor="4"></a>
					</li>
				</ul>
				<div id="fullpage">
					<MainVisual fullPageNum={fullPageNum} />
					<MainWe />
					<MainIno />
					<MainSns />
					<Footer />
				</div>
				{/* //fullpage */}
			</main>
		</>
	);
}
export default Main;
